.top-nav.light-bg {
  color: #000000;
}
.top-nav.light-bg .download-now-btn {
  border-color: #1652F0;
  color: #1652F0;
}
.coming {
  /* border: 1px solid red; */
  font-family: "Noto Sans SC";
  font-weight: 300;
  font-size: 2rem;
  color: #677382;
  display: flex;
  justify-content: flex-start;
  width: 50%;
  margin: 15%;
}
.bot-nav {
  color: #FFFFFF;
}
@media only screen and (max-width: 414px) {
  .coming {
    width: auto;
    justify-content: center;
    /* height: 375px; */
    padding-top: 38.2%;
  }
  .bot-nav {
    color: inherit;
  }
  .illustration-bottom {
    display: none;
  }
}
.footer-aside {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
